@import url('https://fonts.googleapis.com/css?family=Noto+Serif');
#todo{
    /*height:20vh;*/
    width:33%;
    margin-right: auto;
    margin-left: auto;
    -webkit-transition-duration: 0.1s;
         -o-transition-duration: 0.1s;
            transition-duration: 0.1s;
    text-decoration: none;
    -webkit-animation: fadeIn .3s;
            animation: fadeIn .3s;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
#todoForm{
    margin-bottom: 5px;
    background-color : #222222;
}
input{
    background-color: #222222;
}
#notesInput{
margin-top: 3%;
    width: 100%;
    text-align: center;
    outline: none;
    font-family: 'Noto Serif', serif;
    font-size:2em;
    border-bottom: 1px solid #ebdbb2;

    border-right-style: none;
    border-left-style: none;
    border-top-style: none;
    color: #ebdbb2;

}
.outerDivTodo{
    height: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack:justify;
        justify-content:space-between;
    -ms-flex-align: center;
        align-items: center;
    /*border: 1px solid black;*/
    background-color: inherit;
    -webkit-animation: fadeInUp .75s;
            animation: fadeInUp .75s;
    border-bottom: 1px solid #ebdbb2;
    margin: 13px;
    /*padding-bottom: 10px;*/
}
.noteText{

    padding-top: 10px;

    /*max-height: 90%;*/
    /*margin-top: 5px;*/
    /*line-height: 26px;*/

    width:90%;
    resize: none;
    /*next two lines: no cursor */
    font-family: 'Noto Serif', serif;
    color: transparent;
    text-shadow: 0 0 0 #ebdbb2;
    font-size: 1.5em;
    background-color: transparent;
    border: none;

    padding-bottom: 10px;
    /*border: 1px solid black;*/
}

@-webkit-keyframes fadeInUp {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
    }

    to {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, 100%, 0);
                transform: translate3d(0, 100%, 0);
    }

    to {
        opacity: 1;
        -webkit-transform: none;
                transform: none;
    }
}



